a {
  text-decoration: none;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
}

h1, h2, h3, p {
  margin: 0;
}

img {
  display: block;
  height: auto;
  max-width: 100%;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

button {
  border: 0;
}

.container {
  max-width: 375px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 18px;
  padding-right: 18px;

  @include media('min', 'tablet') {
    max-width: 768px;
  }

  @include media('min', 'descktop'){
    min-width: 1200px;
  }
}