.header{
  padding-top: 20px;
  font-family: 'Open Sans', sans-serif;
  text-align: center;
  line-height: 1.3;
  
  &__logo{
    margin-bottom: 10px;
  }

  &__title{
    padding-bottom: 10px;
    
    font-weight: 700;
    font-size: 20px;
    color: $accent-text-color;

    @include media('min', 'tablet'){
      font-size: 26px;
    }
  }
}
